
header {
  font-size: 18px;
  line-height: 24px;
  background: #f3f7f9;
}
.header-top {
  background-color: #293449;
  min-height: 42px;
}
.main-header {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  color: white;
  width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}
.header-logo {
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
  height: 47px;
  width: 220px;
  padding-top: 13px;
  margin-bottom: -5px;
  text-align: center;
  background-color: #ee3643;
}
.poisk {
  position: relative;
  width: 270px;
  height: 42px;
  color: white;
}
.poisk::before {
  content: "";
  position: absolute;
  height: 17px;
  top: 12px;
  left: 17px;
  width: 17px;
  background: url("/src/img/icon-search.png") no-repeat;
}
.poisk-input {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 16px;
  margin-left: 45px;
  background: transparent;
  border: none;
  color: white;
}
input[type="text"]::-webkit-input-placeholder {
  color: white;
}
input[type="text"]::-moz-placeholder {
  color: white;
}
.zakaz {
  background: #63a63e;
}
.header-item {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.header-item li {
  height: 42px;
  padding-top: 8px;
}
.nav-button {
  padding-left: 36px;
  padding-right: 22px;
  font-size: 17px;
  font-weight: 400;
  position: relative;
}
.nav-button1::before {
  content: "";
  top: 2px;
  left: 14px;
  width: 14px;
  height: 16px;
  position: absolute;
  background: url("/src/img/icon-bookmark.png");
}
.nav-button2::before {
  content: "";
  top: 2px;
  left: 12px;
  width: 15px;
  height: 15px;
  position: absolute;
  background: url("/src/img/cart-icon.png");
}
.nav-button__green {
  padding: 9px 13px;
}
.header-info {
  display: flex;
  justify-content: space-between;
  width: 960px;
  padding: 0 65px;
  margin: 46px auto 0px;
}
.info__text {
  width: 200px;
  color: #ee3643;
  font-style: italic;
  margin: 0;
  line-height: 23px;
}
.info__telephone {
  position: relative;
}
.telephone__number {
  border: 3px solid white;
  color: #ee3643;
  text-align: center;
  padding: 8px 54px 8px 74px;
  font-size: 21px;
  line-height: 21px;
  margin: 0;
  margin-bottom: 2px;
}
.telephone__number::before {
  content: "";
  width: 19px;
  height: 19px;
  position: absolute;
  background: url("/src/img/icon-phone.png");
  left: 15px;
}
.info__adress {
  margin: 0 auto;
}
.info__login {
  position: relative;
}
.login-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
  padding-left: 20px;
}
.login-btn::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 17px;
  background: url("/src/img/icon-login.png");
  left: 14px;
}
.register-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
}
.header-navigation {
  margin-top: 44px;
}
.main-navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 960px;
  margin: 0 auto;
  background-color: #32425c;
  padding: 22px 52px;
}
.main-navigation li {
  font-size: 17px;
}

